@import "~normalize.css";
@font-face {
  font-family: 'Mulish';
  src: url('https://fonts.googleapis.com/css2?family=Marcellus&family=Mulish:ital,wght@1,400;1,600;1,700;1,800&display=swap');
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  background-color: #010101;
}
.header_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
}
.header_logo {
  display: block;
}
.header .nav_list {
  display: flex;
  align-items: center;
  list-style: none;
}
.header .nav_item {
  margin-right: 54px;
}
.header .nav_item:last-child {
  margin-right: 0;
}
.header .nav_link {
  display: block;
  font-weight: 700;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  position: relative;
  transition: all 0.2s linear;
}
.header .nav_link::after {
  display: block;
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  z-index: 1;
  background: linear-gradient(264deg, #26fff2 0%, #326cff 96%);
  border-radius: 20px;
  width: 100%;
  height: 2px;
  opacity: 0;
  transition: opacity 0.2s linear;
}
.header .nav_link:hover {
  color: #fff;
  text-shadow: 0 0 0.9px #fff, 0 0 0.9px #fff;
}
.header .nav_link.active {
  color: #fff;
  text-shadow: 0 0 0.9px #fff, 0 0 0.9px #fff;
}
.header .nav_link.active::after {
  opacity: 1;
}
.header .nav_link:hover::after {
  opacity: 1;
}
@media all and (max-width: 768px) {
  .header .nav {
    display: none;
  }
  .header .nav.active {
    display: block;
    margin-bottom: 40px;
  }
  .header .nav.active .nav_list {
    flex-direction: column;
  }
  .header .nav.active .nav_item {
    margin-bottom: 40px;
    text-align: center;
    margin-right: 0;
  }
  .header .nav.active .nav_item:last-child {
    margin-bottom: 0;
  }
  .header .nav.active .nav_link {
    font-size: 35px;
  }
}
@media all and (min-width: 0) and (max-width: 850px) and (orientation: landscape) {
  .header .nav.active .nav_link {
    font-size: 20px;
  }
}
@media all and (max-width: 768px) {
  .header_logo img {
    width: 100px;
  }
  .header.active {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    opacity: 1;
    background-color: #010101;
  }
  .header.active .header_inner {
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    overflow: auto;
  }
  .header.active .header_logo {
    position: absolute;
    top: 15px;
    left: 20px;
    z-index: 11;
  }
}
@media all and (min-width: 0) and (max-width: 850px) and (orientation: landscape) {
  .header.active .header_inner {
    justify-content: flex-start;
  }
}
.burger {
  display: none;
  position: absolute;
  top: 25px;
  right: 20px;
  z-index: 11;
  width: 40px;
  height: 28px;
  border: 0;
  background: transparent;
  padding: 0;
}
@media all and (max-width: 768px) {
  .burger {
    display: block;
  }
}
.burger_item {
  display: block;
  width: 100%;
  height: 4px;
  margin-bottom: 8px;
  background-color: #fff;
  border-radius: 20px;
  transition: all 0.2s linear;
}
.burger_item:nth-child(1) {
  width: 50%;
}
.burger_item:nth-child(2) {
  width: 100%;
}
.burger_item:nth-child(3) {
  width: 75%;
}
.burger.active .burger_item:nth-child(1) {
  transform-origin: left top;
  transform: rotate(45deg) translateY(-2px);
  width: 100%;
}
.burger.active .burger_item:nth-child(2) {
  opacity: 0;
}
.burger.active .burger_item:nth-child(3) {
  transform-origin: bottom left;
  transform: rotate(-45deg) translateY(2px);
  width: 100%;
}
.section {
  overflow: hidden;
}
.section_inner {
  margin: 60px 0;
}
.section_title {
  display: inline-block;
  font-size: 28px;
  margin-bottom: 47px;
  padding: 0 19px;
  color: #fff;
  position: relative;
}
.section_title a {
  color: inherit;
  text-decoration: none;
}
.section_title::before {
  display: block;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100%;
  width: 8px;
  background: linear-gradient(264deg, #26fff2 0%, #326cff 96%);
  border-radius: 50px;
  transition: width 0.4s ease-in-out;
}
.section_title:hover::before {
  width: 100%;
}
.section_content {
  margin-bottom: 55px;
}
.section_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
}
.section_item {
  margin-bottom: 40px;
  flex: 0 0 14%;
}
.section_link {
  display: block;
  width: 100%;
  border-radius: 20px;
  overflow: hidden;
  transition: box-shadow 0.2s linear;
}
.section_link:hover {
  box-shadow: 0 0 20px rgba(38, 255, 242, 0.5);
}
.section_link:hover img {
  transform: scale(1.1);
}
.section_link img {
  display: block;
  max-width: 100%;
  height: auto;
  transition: transform 0.8s linear;
}
@media all and (max-width: 991px) {
  .section_list {
    margin-left: -5%;
  }
  .section_item {
    flex-basis: 32%;
  }
  .section_item:nth-child(-n+3) {
    margin-bottom: 0;
  }
  .section_item img {
    min-width: 100%;
  }
  .section_title {
    font-size: 25px;
  }
}
@media all and (max-width: 768px) {
  .section_list {
    justify-content: space-between;
    margin-left: -5%;
  }
  .section_item {
    flex-basis: 48%;
  }
  .section_item:nth-last-child(-n+3) {
    margin-bottom: 40px;
  }
  .section_item:nth-last-child(-n+2) {
    margin-bottom: 0;
  }
  .section_title {
    font-size: 20px;
  }
}
@media all and (max-width: 480px) {
  .section_list {
    margin-left: -10%;
  }
}
@media all and (max-width: 390px) {
  .section_list {
    margin-left: -15%;
  }
}
.genres_list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  list-style: none;
}
.genres_item {
  flex: 0 0 24%;
  border-radius: 20px;
  text-align: center;
  margin-bottom: 27px;
  transition: filter 0.2s linear;
}
.genres_item:hover {
  filter: invert(100%);
}
.genres_item a {
  display: block;
  text-decoration: none;
  padding: 45px 0;
}
.genres_item img {
  margin: 0 auto;
  margin-bottom: 8px;
}
.genres_item--romance {
  background: linear-gradient(109.31deg, #FD093F 2.4%, #F383F1 100%);
}
.genres_item--drama {
  background: linear-gradient(114.5deg, #0FFFDA 2.22%, #3CDB77 68.84%);
}
.genres_item--historical {
  background: linear-gradient(109.31deg, #B936FF 2.4%, #57DEDA 100%);
}
.genres_item--action {
  background: linear-gradient(109.31deg, #FD093F 2.4%, #FB1378 2.41%, #FCCB1A 100%);
}
.genres_item--sci-fi {
  background: linear-gradient(109.31deg, #FF6472 2.4%, #FDA75D 100%);
}
.genres_item--sci-fi img {
  transform: rotate(45deg);
  width: 30%;
}
.genres_item--horror {
  background: linear-gradient(110.16deg, #13547A 2.37%, #80D0C7 96.77%);
}
.genres_item--comedy {
  background: linear-gradient(109.93deg, #FFF77B 2.37%, #FFBF42 50%);
}
.genres_item--documentary {
  background: linear-gradient(109.31deg, #1FA2FF 2.4%, #1FA2FF 2.41%, #1F535C 97.46%);
}
.genres_item-title {
  font-size: 28px;
  font-weight: 700;
  color: #fff;
}
@media all and (max-width: 991px) {
  .genres_item {
    flex-basis: 48%;
  }
  .genres_item--sci-fi img {
    transform: rotate(45deg);
    width: 20%;
  }
  .genres_list {
    margin-left: -5%;
  }
}
@media all and (max-width: 768px) {
  .genres_list {
    margin-left: -5%;
  }
  .genres_item-title {
    font-size: 20px;
  }
}
@media all and (max-width: 480px) {
  .genres_item {
    flex-basis: 100%;
  }
  .genres_list {
    margin-left: -10%;
  }
  .genres__link {
    padding: 25px 0;
  }
  .genres_item-title {
    font-size: 16px;
  }
}
@media all and (max-width: 390px) {
  .genres_list {
    margin-left: -15%;
  }
}
.people_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
}
.people_person {
  font-size: 18px;
  font-weight: 700;
  color: #fff;
  text-align: center;
  flex: 0 0 14%;
  margin-bottom: 40px;
}
.people_person a {
  display: block;
  color: inherit;
  text-decoration: none;
}
.people_person img {
  display: block;
  max-width: 100%;
  height: auto;
  border-radius: 50%;
  margin: 0 auto;
  margin-bottom: 23px;
  filter: brightness(0.4);
  transition: filter 0.2s linear;
}
.people_person:hover img {
  filter: brightness(1);
}
@media all and (max-width: 991px) {
  .people_person {
    flex-basis: 32%;
  }
  .people_list {
    margin-left: -10%;
  }
}
@media all and (max-width: 768px) {
  .people_person {
    font-size: 16px;
  }
  .people_list {
    margin-left: -10%;
  }
}
@media all and (max-width: 390px) {
  .people_list {
    margin-left: -15%;
  }
}
.announce {
  margin: 100px 0;
}
.announce_inner {
  border-radius: 20px;
  display: flex;
  background: linear-gradient(to right, #0D1A73, #000000);
  overflow: hidden;
  text-decoration: none;
}
.announce_image-block {
  width: 50%;
}
.announce_text-block {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px 0;
}
.announce_text-block img {
  width: 50%;
  margin-bottom: 20px;
}
@media all and (max-width: 768px) {
  .announce {
    margin: 60px 0;
  }
  .announce_inner {
    flex-wrap: wrap;
  }
  .announce_image-block {
    width: 100%;
  }
  .announce_text-block {
    width: 100%;
  }
}
.footer {
  background-color: #101012;
}
.footer-inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 70px 0;
}
.footer_col {
  flex: 0 0 19%;
}
.footer_list {
  list-style: none;
}
.footer_list_link {
  display: block;
  font-size: 18px;
  font-weight: 600;
  line-height: 2;
  color: #C2C2C2;
  text-decoration: none;
  position: relative;
}
.footer_list_link::after {
  display: block;
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 2px;
  opacity: 0;
  background: linear-gradient(264deg, #26fff2 0%, #326cff 96%);
  transition: opacity 0.2s linear;
}
.footer_list_link:hover {
  color: #fff;
}
.footer_list_link:hover::after {
  opacity: 1;
}
@media all and (max-width: 991px) {
  .footer_col {
    flex-basis: 50%;
    margin-bottom: 40px;
  }
  .footer_col:first-child {
    flex-basis: 100%;
    justify-content: center;
    text-align: center;
  }
}
@media all and (max-width: 768px) {
  .footer-inner {
    padding: 40px 0;
  }
  .footer_col {
    flex-basis: 50%;
    margin-bottom: 40px;
  }
  .footer_col:first-child {
    flex-basis: 100%;
    justify-content: center;
    text-align: center;
  }
}
@media all and (max-width: 480px) {
  .footer_col {
    flex-basis: 100%;
    margin-bottom: 40px;
    margin-left: -40px;
  }
}
.control_list {
  display: flex;
  list-style: none;
  align-items: center;
}
.control_item {
  margin-right: 75px;
}
.control_item .btn {
  background-color: transparent;
  border: none;
  display: block;
  background-size: cover;
  background-repeat: no-repeat;
}
.control_item .btn-search {
  background-image: url(../assets/search.svg);
  width: 1rem;
  height: 1rem;
}
.control_item .btn-search:hover {
  cursor: pointer;
}
.control_item .btn-notify {
  background-image: url(../assets/bell.svg);
  width: 1.2rem;
  height: 1.35rem;
}
.control_item .btn-notify:hover {
  cursor: pointer;
}
.control_item .btn-lk {
  background-image: url(../assets/avatar__icon.svg);
  width: 1rem;
  height: 1.2rem;
}
.control_item .btn-lk:hover {
  cursor: pointer;
}
.control_item:last-child {
  margin-right: 0;
}
.control_item .btn {
  transition: all 0.2s linear;
}
.control_item .btn:hover {
  color: #2CB7F8;
}
@media all and (max-width: 768px) {
  .control {
    display: none;
  }
  .control.active {
    display: block;
  }
  .control.active .btn {
    color: rgba(255, 255, 255, 0.5);
  }
}
@media all and (min-width: 0) and (max-width: 850px) and (orientation: landscape) {
  .control.active .control_item {
    font-size: 20px;
  }
  .control.active .control_item::after {
    font-size: 20px;
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  font-family: 'Mulish', sans-serif;
  background-color: #010101;
  color: #fff;
  font-size: 15px;
}
body.no-scroll {
  overflow: hidden;
}
body::-webkit-scrollbar {
  width: 0.6rem;
}
body::-webkit-scrollbar-track {
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: transparent;
  border-radius: 1rem;
}
body::-webkit-scrollbar-thumb {
  border-radius: 1rem;
  background: #26fff2;
  background: linear-gradient(264deg, #26fff2 0%, #326cff 96%);
  transition: background 0.2s linear;
}
body::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(to right bottom, #0039cb, #00bfb4);
}
body::-ms-scrollbar {
  width: 0.6rem;
}
body::-ms-scrollbar-track {
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: transparent;
  border-radius: 1rem;
}
body::-ms-scrollbar-thumb {
  border-radius: 1rem;
  background: #26fff2;
  background: linear-gradient(264deg, #26fff2 0%, #326cff 96%);
}
body::-moz-scrollbar {
  width: 0.6rem;
}
body::-moz-scrollbar-track {
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: transparent;
  border-radius: 1rem;
}
body::-moz-scrollbar-thumb {
  border-radius: 1rem;
  background: #26fff2;
  background: linear-gradient(264deg, #26fff2 0%, #326cff 96%);
}
.container {
  max-width: 1440px;
  padding: 0 20px;
  margin: 0 auto;
}
.main {
  margin-top: 100px;
}
a,
button {
  transition: all 0.2s linear;
}
a:focus-visible,
button:focus-visible {
  outline: 0;
  box-shadow: 0 0 0 5px #010101, 0 0 0 10px #2CB7F8;
}
.btn {
  background: transparent;
  border: none;
  border-radius: 100px;
  font-family: 'Mulish', serif;
  font-weight: 800;
  font-size: 16px;
  color: #fff;
  padding: 15px 30px;
  transition: transform 0.2s linear, box-shadow 0.2s linear;
  cursor: pointer;
}
.btn--blue {
  box-shadow: 0 4px 50px rgba(50, 108, 255, 0.5);
  background: linear-gradient(264deg, #26fff2 0%, #326cff 96%);
  position: relative;
  padding-left: 50px;
}
.btn--blue::after {
  content: '';
  display: block;
  width: 14px;
  height: 18px;
  background-image: url(../assets/play.svg);
  position: absolute;
  left: 20px;
  top: calc(50% - 9px);
}
.btn--white {
  background-color: #fff;
  color: #323232;
}
.btn--wi > i {
  margin-right: 15px;
}
.btn--empty {
  padding: 0;
  border-radius: 0;
}
.btn:hover {
  transform: scale(1.1);
}
.btn + .btn {
  margin-left: 30px;
}
a::selection,
p::selection {
  color: #fff;
}
.banner {
  border-bottom: 2px solid #2F2E2E;
}
.banner_inner {
  background: url('../assets/hero.png') no-repeat;
  background-position: right;
  height: 766px;
  border-radius: 20px;
}
.banner_text-block-wrapper {
  max-width: 562px;
  width: 100%;
  height: 100%;
  background-color: #000;
  box-shadow: 100px 0 100px #000;
  position: relative;
}
.banner_text-block {
  position: absolute;
  top: 10%;
  left: 0;
  z-index: 1;
  padding: 0 15px;
}
.banner_title {
  font-family: 'Mulish', serif;
  font-size: 80px;
  margin-bottom: 36px;
}
.banner_info {
  color: #fff;
  font-weight: 800;
  margin-bottom: 32px;
}
.banner_info span {
  white-space: nowrap;
  margin-right: 18px;
  margin-bottom: 20px;
}
.banner_info span:last-child {
  margin-right: 0;
}
.banner_info_age {
  display: inline-block;
  position: relative;
  width: 60px;
  text-align: center;
}
.banner_info_age::before {
  display: block;
  content: '';
  width: 100%;
  height: 24px;
  background-image: url('../assets/age.svg');
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  z-index: 1;
}
.banner_info_ratting {
  margin-left: 10px;
  position: relative;
}
.banner_info_ratting::after {
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  background-image: url(../assets/start.svg);
  background-size: cover;
  position: absolute;
  top: calc(50% - 8px);
  right: -26px;
}
.banner_text {
  font-size: 18px;
  line-height: 28px;
  color: #C2C2C2;
  margin-bottom: 35px;
}
@media all and (max-width: 991px) {
  .banner_text-block-wrapper {
    max-width: 350px;
  }
  .banner_title {
    font-size: 50px;
  }
  .banner-btns button:last-child {
    /*margin-left: 0;*/
    margin-top: 30px;
  }
  .banner .btn + .btn {
    margin-left: 7px;
  }
}
@media all and (max-width: 768px) {
  .banner_inner {
    background: linear-gradient(rgba(0, 0, 0, 0.5)), url('../assets/hero.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  .banner_text-block-wrapper {
    width: 100%;
    background: none;
    box-shadow: none;
  }
  .banner_text-block {
    top: auto;
    bottom: 10%;
  }
  .banner_title {
    font-size: 40px;
  }
  .banner .btn + .btn {
    margin-left: 7px;
  }
}
@media all and (max-width: 390px) {
  .banner_title {
    font-size: 30px;
  }
  .banner_text {
    font-size: 15px;
    line-height: 1.5;
  }
  .banner .banner_text-block {
    bottom: 20%;
  }
}
.slick-dots li button:before {
  color: #fff;
}
